.NewToDoForm {
	margin-top: 3rem;
	display: flex;
	flex-wrap: wrap;
}

.NewToDoForm label {
	min-width: 100%;
	margin-bottom: 0.5rem;
	font-size: 1.3rem;
}

.NewToDoForm input {
	flex-grow: 1;
	border: none;
	background: #f7f1f1;
	padding: 0 1.5em;
	font-size: initial;
}

.NewToDoForm button {
	padding: 0;
	border: none;
	background: #ff6666;
	color: white;
	text-transform: uppercase;
	font-weight: bold;
	border: 1px solid rgba(255, 255, 255, 0.3);
	margin-left: 5px;
	cursor: pointer;
	transition: background 0.2 ease-out;
}

.NewToDoForm input,
.NewToDoForm button {
	font-family: "permanent marker", sans-serif;
	height: 3rem;
}
